import React, { Component } from 'react';
import { Button, Container, Form, FormGroup, Input, Row } from 'reactstrap';

export default class SearchBarYG extends Component {
	render() {
		return (
			<div className="header-search-bar">
				<Container>
					<Row>
						<section id="wb-srch" className="col-sm-6 offset-sm-3 header-search">
							<h2>Search</h2>
							<div className="region region-search-bar">
									<section id="block-search-form" className="block block-search clearfix">
										<Form className="search-form form-search content-search" role="search" action="/" method="post" id="search-block-form" acceptCharset="UTF-8" target="_self">
											<FormGroup>
												<h2 className="element-invisible">Search form</h2>
												<div className="input-group">
													<Input title="Enter the terms you wish to search for." className="custom-search-box form-control form-text" placeholder="Search" aria-label="Search" id="edit-search-block-form--2" name="search_block_form" value="" size="15" maxLength="128" type="text" />
													<span className="input-group-btn">
														<Button type="submit" className="btn btn-primary" value="Search" aria-label="Search">
															<span className="icon glyphicon glyphicon-search" aria-hidden="true"></span>
														</Button>
													</span>
												</div>
												<div className="form-actions form-wrapper form-group" id="edit-actions">
													<Button className="element-invisible btn btn-default form-submit" type="submit" id="edit-submit" name="op" value="Search">Search</Button>
												</div>
												<Input name="form_build_id" value="form-8Kc5uxswUPJsI0nufqOGtz9hf0TwKHwcuQ4rhDlmCeA" type="hidden" />
												<Input name="form_id" value="search_block_form" type="hidden" />
												<Input name="custom_search_paths" value="search?query=[key]" type="hidden" />
											</FormGroup>
										</Form>
									</section>
								</div>
						</section>
					</Row>
				</Container>
			</div>
		);
	}
}



