import React, { Component } from 'react';
import { Col, Container, Row } from 'reactstrap';


export default class ContentMainYG extends Component {
	render() {
		var styles = {
			minHeight: '600px',
		}
	
		return (
			<Container>
				<Row>
					<Col style={styles}>
						Start developing your react app
					</Col>
				</Row>
			</Container>
		);
	}
}



