import React, { Component } from 'react';
import { Col, Container, Row } from 'reactstrap';

import ContentMain from './ContentMain';

export default class Home extends Component {
	render() {
		return (
			<Container>
				<Row>
					<Col>
						<ContentMain />
					</Col>
				</Row>
			</Container>
		);
	}
}




