import React, { Component } from 'react';

export default class MenuButton extends Component {
	render() {
		return (
			<button onMouseDown={this.props.handleMouseDown} aria-controls="global-nav" aria-haspopup="true" id="menu-button" title="Search and menus" aria-label="Menu collapsed">
				<span className="menu-icon" aria-hidden="true">
					<svg width="30" height="30" viewBox="0 0 30 30" xmlns="http://www.w3.org/2000/svg">
						<g fill="#323232" fillRule="evenodd">
							<rect width="30" height="6" rx="2"></rect>
							<rect y="12" width="30" height="6" rx="2"></rect>
							<rect y="24" width="30" height="6" rx="2"></rect>
						</g>
					</svg>
				</span>
				<p className="gov-serv">Menu</p>
			</button>
		);
	}
}




