import React, { Component } from 'react';

export default class LogoYG extends Component {
	render() {
		return (
			<div>
				<a href="/">
					<object id="header-logo" data="/images/logo.svg" role="img" type="image/svg+xml" alt="Government of Yukon Logo" tabIndex="-1">
						<img alt="Government of Yukon Logo" src="/images/logo.png" />
					</object>
				</a>
			</div>
		);
	}
}



