import React, { Component } from 'react';
import { Col, Container, Row } from 'reactstrap';
import Favicon from 'react-favicon';
import Logo from './containers/Logo';
import SearchBar from './containers/SearchBar'
import MenuButton from './containers/MenuButton';
import Menu from './containers/Menu';

export default class Header extends Component {
	render() {
		return(
				<div>
					<Favicon url="/favicon.ico" />
					<div id="wb-bnr">
						<Container>
							<Row>
								<Col xs={6} className='site-main-logo'>
									<Logo />
								</Col>
								<Col xs={6} className="site-nav-toggle">
									<Row>
										<div className="pull-right">
											<section id="wb-lng" className="header-lang-toggle">
												<ul className="text-right"><li id="wet-fullhd-lang"><a href="/fr" className="link" hrefLang="fr" lang="fr">Français</a></li></ul>
											</section>
											<MenuButton />
											<Menu />
										</div>
									</Row>
								</Col>
							</Row>
						</Container>
					</div>
					<SearchBar />
				</div>
			);
	}
}



