import React, { Component } from 'react';
import { Col, Container, Row } from 'reactstrap';

export default class NotFound extends Component {
	render() {
		return (
			<Container>
				<Row>
					<Col xs={12}>
						<h3>Sorry, page not found!</h3>
					</Col>
				</Row>
			</Container>
		);
	}
}


