import React from 'react';
import { Nav, NavItem, NavLink } from 'reactstrap';
import { slide as Menu } from 'react-burger-menu';

import data from './menu-data.json';

export default props => {
	var styles = {
		bmOverlay: {
			width: '100vw',
			left: '0',
			top: '0'
		},
		bmMenuWrap: {
			background: '#ffffff',
			top: '0px'
		},
		bmCrossButton: {
			width: '90px',
			height: '50px',
			right: '80px',
			top: '40px',
			zIndex: '1100'
		}
	}

	return (
		<Menu 
			width={ '60%' } 
			styles={ styles } 
			{...props} 
			customCrossIcon={<div>close <svg viewBox="0 0 40 40"><path class="close-x" d="M 10,10 L 30,30 M 30,10 L 10,30"></path></svg></div>}
			right
		>
			<nav id="react-nav" aria-label="Main navigation" aria-hidden="true">
				<header>
					<h3 className="gov-serv-h3">Transactional services <br />components</h3>
				</header>
				<h2 className="visuallyhidden">Topics menu</h2>
				<div className="region region-navigation">
					<Nav className="menu" role="menubar" >
						{
							data.map(function(link) {
								return <NavItem className='leaf' key={link.key}><NavLink href={link.url}>{link.key}</NavLink></NavItem>
							})
						}
					</Nav>
				</div>
			</nav>
		</Menu>
	);
}






