import React, { Component } from 'react';
import { Col, Container, Nav, Row } from 'reactstrap';

export default class Footer extends Component {
	render() {
		return(
			<div>
				<Container>
					<Row>
						<Nav className="col-md-10 ftr-urlt-lnk">
							<h2 className="wb-inv">About this site</h2>
						</Nav>
					</Row>
				</Container>
				<div className="brand">
					<Container>
						<Row>
							<Col xs={12} className="brand-items">
								<div className="site-main-logo">
									<object id="footer-logo" data="/images/logo-white.svg" role="img" type="image/svg+xml" alt="Government of Yukon Logo" tabIndex="-1">
										<img alt="Government of Yukon Logo" src="/images/logo-white.png" />
									</object>
								</div>
							</Col>
						</Row>
					</Container>
				</div>
				<div className="footer-menu-wrapper">
					<Container>
						<Row>
							<Col xs={6} className="d-flex flex-row">
								<Nav className="footer-menu navbar-left">
									<section id="block-menu-menu-wet-footer" className="block block-menu clearfix">
										<h2 className="block-title block-title block-title">Footer menu</h2>
										<ul className="nav navbar-nav">
											<li className="first leaf active active"><a href="/en" className="active active active active">Home</a></li>
											<li className="leaf"><a href="/en/news">News</a></li>
											<li className="leaf"><a href="/en/copyright">Copyright</a></li>
											<li className="leaf"><a href="/en/privacy-statement">Privacy statement</a></li>
											<li className="last leaf"><a href="/en/disclaimer">Disclaimer</a></li>
										</ul>
									</section>
								</Nav>
							</Col>
							<Col xs={6} className="d-flex flex-row-reverse">
								<div className="site-desc-wrap navbar-right">
									<p className="site-desc">© Copyright 2018 <a href="/">Government of Yukon</a></p>
								</div>
							</Col>
						</Row>
					</Container>
				</div>
			</div>
		);
	}
}


